shadow: #0f0 0px 0px 10px 1px;\n  }\n  #burger .bun.top {\n    top: 25%;\n  }\n  #burger .bun.bottom {\n    top: 75%;\n  }\n  #burger .filling {\n    top: 50%;\n    transform: translate(-50%, -50%) rotate(180deg);\n    -webkit-animation: green-ls-out 0.8s ease forwards;\n            animation: green-ls-out 0.8s ease forwards;\n  }\n  #burger.active .bun {\n    border-radius: 3px;\n    top: 50%;\n    transition: top 0.4s ease, transform 0.4s ease;\n  }\n  #burger.active .bun.top {\n    transform: translate(-50%, -50%) rotate(-225deg);\n  }\n  #burger.active .bun.bottom {\n    transform: translate(-50%, -50%) rotate(405deg);\n  }\n  #burger.active .filling {\n    transform: translate(-50%, -50%) rotate(-90deg);\n    -webkit-animation: green-ls-in 0.8s ease forwards;\n            animation: green-ls-in 0.8s ease forwards;\n  }\n\n  nav {\n    font-family: Open Sans;\n    color: white;\n    background: #111;\n    position: absolute;\n    transform: translatex(-100%);\n    transition: transform 0.2s ease;\n    top: 80px;\n    bottom: 0px;\n    padding-right: 16px;\n    width: 300px;\n  }\n  nav.show {\n    transform: translatex(0%);\n  }\n  nav ul {\n    list-style: none;\n    padding: 0;\n    margin: 0;\n  }\n  nav ul li {\n    background: #ecfcff;\n    box-shadow: #4df 0px 0px 0px 2px;\n    font-size: 1.75em;\n    line-height: 1.25em;\n    padding: 0px;\n    padding-left: 0px;\n    margin: 16px 0px;\n    width: 0;\n    border-radius: 0px 100px 100px 0px;\n    transition: width 0.2s ease, padding 0.2s ease, color 0.2s ease, text-shadow 0.2s ease;\n  }\n  nav ul li:hover {\n    box-shadow: #4df 0px 0px 20px 2px;\n    text-shadow: #4df 0px 0px 6px, #4df 0px 0px 10px;\n  }\n  nav ul li:hover {\n    width: 100%;\n    padding: 0px 16px 0px 0px;\n    color: black;\n  }\n  nav ul li a {\n    display: block;\n    text-decoration: none;\n    width: 300px;\n    color: inherit;\n    padding-left: 16px;\n  }\n  nav ul li.green {\n    background: #e6ffe6;\n    box-shadow: #0f0 0px 0